import { Cell } from './cell';

export class Section {
  cells: Cell[];
  position: number;

  resetSimilar() {
  	this.cells.forEach(function(cell) {
  		cell.similar = false;
  	});
  }

  showSimilar(val) {
  	this.resetSimilar();
  	this.cells.forEach(function(cell) {
  		if (cell.val == val){
  			cell.similar = true;
  		} else {
  			cell.similar = false;
  		}
  	});
  }

  setValue(value, position) {
  	this.cells[position].val = value;
  }

  getNumberOfEmpty() {
  	let empty = 0;
  	this.cells.forEach(function(el) {
  		if (el.val){
  			empty++;
  		}
  	});
  	return empty;
  }

  constructor(cells, position) {
  	this.cells = cells;
  	this.position = position;
  }
}