export class Cell{
  similar: boolean = false;
  val: number;
  prediction: number[] = [];
  nonUsedPredictions: number[] = [];
  position: number;
  allowedRowSections: number[] = [];
  allowedRowPositions: number[] = [];
  allowedColSections: number[] = [];
  allowedColPositions: number[] = [];

  setPrediction() {
  	if (this.val) {
  		return;
  	}
  	for (let i = 1; i <= 9; i++) {
  		this.prediction.push(i);
  	}
  	this.refreshPredictions();
  }

  setNumber() {
  	if (this.prediction.length === 1){
  		this.val = this.prediction[0];
  	}
  }

  removePrediction(prediction) {
  	this.nonUsedPredictions.push(prediction);
  	this.refreshPredictions();
  }

  refreshPredictions() {
  	let self = this;
  	this.prediction = this.prediction.filter( function( el ) {
		return !self.nonUsedPredictions.includes( el );
	});
	this.prediction = Array.from(new Set(this.prediction));
  }

  setAllowedRowSections(sections) {
  	this.allowedRowSections = sections;
  }

  setAlloweRowPositions(positions) {
  	this.allowedRowPositions = positions;
  }

  setAllowedColSections(sections) {
  	this.allowedColSections = sections;
  }

  setAllowedColPositions(positions) {
  	this.allowedColPositions = positions;
  }


  constructor(val, position) {
  	this.val = val;
  	this.position = position;
  	this.setPrediction();
  }
}